import React from 'react';
import styles from './CharacterDetail.module.css';

const CharacterDetail = ({character, deleteCharacter}) => {
    var classImgHref = ""; 
    console.log(character.class);
    switch(character.class) {
        case "Bow Master": case "Marksman":
            classImgHref = "%PUBLIC_URL%/images/jobicons/bowman.png";
            break;
        case "Arch Mage (I/L)": case "Arch Mage (F/P)": case "Bishop":
            classImgHref = "%PUBLIC_URL%/images/jobicons/magician.png";
            break;  
        case "Buccaneer": case "Corsair":
            classImgHref = "%PUBLIC_URL%/images/jobicons/pirate.png";
            break;  
        case "Night Lord": case "Shadower":
            classImgHref = "%PUBLIC_URL%/images/jobicons/thief.png";
            break; 
        case "Hero": case "Paladin": case "Dark Knight":
            classImgHref = "%PUBLIC_URL%/images/jobicons/warrior.png";
            break; 
        default:
            classImgHref = "%PUBLIC_URL%/images/jobicons/all.png";
            break; 
    }

    return (
        <div className={styles.CharacterDetail}>
            <div>
                <img src={classImgHref} alt="Class Type"></img>
            </div>
            <div className={styles.stackedDetail}>
                <p className={styles.ign}>{character.ign}</p>
                <p className={styles.ign}>{character.class}</p>
            </div>
            <div className={styles.stackedDetail}>
                <p>Lvl.</p>
                <p className={styles.level}>{character.level}</p>
            </div>
            <div>
                <button type="button" onClick={deleteCharacter}>
                    Delete    
                </button>
            </div>
        </div>
    )
}

export default CharacterDetail;